<template>
  <div class="achievement">
    <Card class="tagTitle">      
      <Tag type="border" class="tag" v-for="(item,index) in tagData" :color="tagColor[index%4]" :key="index" :name="index" closable @on-close="deletTag">
          {{item.name}}
      </Tag><br>
      <Button class="add" icon="ios-plus-empty" type="dashed" size="small" @click="handleAdd">添加分类</Button>
    </Card>
    <Card>
        <p slot="title">
            成就列表
        </p>
        <div style="text-align:center;">
            <Table :loading="loading" :columns="columns" :data="achievementData" stripe></Table>           
        </div>
    </Card>
  </div>
</template>

<script>
import filters from '@/filters/index'
export default {
    name: 'achievement',
    data () {
      return {
        loading: false,
        columns:[{
          title: "作者",
          key: "username"
        },{
          title: "发表时间",
          key: "post_time",
          render: (h, params) => {                         
              return h('div',
                  filters.timeFormat(params.row.post_time) 
              )
          } 
        },{
          title: "成就描述",
          key: "description"
        },{
            title: '删除',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
                return h('div', [                                
                    h('Button', {
                        props: {
                            type: 'error',
                            size: 'small'
                        },
                        on: {
                            click: () => {
                                this.delAchievement(this.achievementData[params.index].id,params.index);
                            }
                        }
                    }, '删除')
                ]);
            }
        }],
        achievementData: [],
        tagData: [],
        tagColor:['blue','green','red','yellow']
      }
    },
    created () {
      this.sellectAllTag();
      this.selectAllAchievement();
    },
    methods: {
      selectAllAchievement () {
        this.$http.get('/achievement/achievementInfo')
          .then(res => {
            this.achievementData = res.data.msg;
          }).catch(err => {
            this.$Message.error("出错啦！");  
          })
      },
      sellectAllTag () {
        this.$http.get('/achievement/selectAllTag')
          .then(res => {
            this.tagData = res.data.msg;
          }).catch(err => {
            this.$Message.error("出错啦");
          })
      },
      deletTag (event,index) {          
        this.$Modal.confirm({
            content: '<p>确定删除吗</p>',
            onOk: () => {                
              this.$http.post('/achievement/delTag',{
                  'tagId': this.tagData[index].achievement_num
              }).then(res => {
                  this.$Modal.remove();
                  this.$Message.info('删除成功！');                        
                  this.tagData.splice(index,1);
              }).catch(err => {
                  his.$Modal.remove();
                  this.$Message.error('删除失败！');
              })
            },
            onCancel: () => {
                this.$Message.info('Clicked cancel');
            }
        })
      },
      handleAdd () {
        this.$Modal.confirm({
            render: (h) => {
                return h('Input', {
                    props: {
                        value: this.value,
                        autofocus: true,
                        placeholder: '请输入新的分类名称'
                    },
                    on: {
                        input: (val) => {                                
                            this.value = val;
                        }
                    }                        
                })
            },
            loading: true,
            onOk: () => {
                if(this.value == null){
                    this.$Modal.remove();
                    this.$Message.error('分类名不能为空！');
                }else{
                    this.$http.post('/achievement/addTag',{
                        'tagName': this.value
                    }).then(res => {                        
                        this.$Modal.remove();
                        this.sellectAllTag();
                        this.$Message.info("添加成功！");                        
                        this.value = "";
                    }).catch(err => {
                        this.$Modal.remove();
                        this.$Message.error("添加失败！");    
                    })
                }
            },
            onCancel: () => {
                this.value = ""
            }
        })
      },
      delAchievement(id,index){        
        this.$http.post('/achievement/delAchievement',{
          "achievementId": id
        }).then(res => {
          this.achievementData.splice(index,1);
          this.$Message.info("删除成功！")
        }).catch(err => {
          this.$Message.info("出错啦！")
        })
      }
    }
}
</script>
<style lang="scss" scoped>
.tagTitle{
  margin-top: 20px;
  .tag{
    margin-right: 20px;
  }
}

.add{
  margin-top: 20px;
}
</style>




